}

.link {
  font-family: 'LeOsler';
  font-size: large;
  font-weight: bold;
}

.logo {
  margin: auto;
  scale: 0.5;
}

.video-container {
  position: relative;
  padding-bottom: 56.25%;
  padding-top: 30px;
  height: 0;
  overflow: hidden;
}

.video-container iframe,
.video-container object,
.video-container embed {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.dates_title {
  text-align: center;
  padding-bottom: 10px;
}

.dates_card {
  text-align: left;
